.cards {
    padding: 4rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
  }
  
  h2 {
    margin: 0px;
    padding: 0px;
  }
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 100%;
    margin: 0 auto;
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards__items {
    margin-bottom: 24px;
    height: auto;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    /* overflow: hidden; */
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    /* overflow: hidden; */
  }


  .interview__list__question {
    margin-top: 0px;
    color: red;
  }

  .interview__list__answer {
    margin-top: 0px;
    color: black;
  }


.interview__list__intro {
  margin-top: 0px;
  color: black;
  
}

  .cards__item__pic-wrapBlog {
    position: relative;
    width: 30%;
    padding: 20px;
    /* overflow: hidden; */
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }

  .cards__item__imgBlog {
    /* position: relative; */
    /* display: flex; */
    width: 250px;
    max-width: 30%;
    height: 180px;;
    /* max-height: 30%; */
    float: left;
    padding: 3px;
    margin: 10px;
  }

  .cards__item__imgBlogRight {
    /* position: relative; */
    /* display: flex; */
    width: 250px;
    max-width: 30%;
    height: 180px;;
    /* max-height: 30%; */
    float: right;
    padding: 3px;
    margin: 10px;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px;
    height: auto;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  .button_book button {
    background-color: black;
    width: 120px;
    color: white;
    height: 90px;
    margin: 10px 2.5px;
    padding: 10px;
    font-size: 24px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .button_book:hover {
    background-color: rgb(rgb(235, 22, 235), rgb(83, 16, 190), rgb(133, 14, 231));
    transition: all 0.2s ease-out;
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }
  